<template>
    <div id="root">
        <div class="todo-container">
            <div class="todo-wrap">
                <MyHeaderVue v-on:AddTodo="AddTodo"></MyHeaderVue>
                <MyListVue :todos="todos" :DeleteTodo="DeleteTodo"/>
                <MyfooterVue :todos="todos" @CheckAllTodo="CheckAllTodo" @ClearAllTodo="ClearAllTodo"/>
            </div>
        </div>
    </div>
</template>

<script>
import pubsub from 'pubsub-js'
import MyfooterVue from './components/Myfooter.vue';
// import MyItemVue from './components/MyItem.vue';
import MyListVue from './components/MyList.vue';
import MyHeaderVue from './components/MyHeader.vue';
export default {
    name:'App',
    components:{MyfooterVue,MyListVue,MyHeaderVue},
    data(){
        return {
            todos:JSON.parse(localStorage.getItem('todos'))||[]
        }
    },
    methods:{
        //添加todo
        AddTodo(x){
            // console.log(x);
            this.todos.unshift(x);
        },
        //勾选or取消勾选todo
        CheckTodo(id){
            this.todos.forEach((todo) => {
                if(todo.id===id)todo.completed=!todo.completed
            });
        },
        // 删除一个todo
        DeleteTodo(msgName,id){
            this.todos=this.todos.filter(todo=>{
                return todo.id!==id;
            })
            // this.todos.forEach((todo)=>{
            //     if(todo.id===id){}
            // })
        },
        UpdataTodo(id,title){
            this.todos.forEach((todo)=>{
                if(todo.id==id)todo.title=title
            })
        }
        ,
        //全选or取消全选
        CheckAllTodo(done){
            this.todos.forEach((todo)=>{
                todo.completed=done;
            })
        },
        ClearAllTodo(){
            this.todos=this.todos.filter((todo)=>{
                return todo.completed!==true;
            })
        }
    },
    watch:{
        todos:{
            deep:true,
            handler(value){
                localStorage.setItem('todos',JSON.stringify(value))
            },
            
        }
    },
    mounted(){
        this.$bus.$on('CheckTodo',this.CheckTodo)
        this.pubId=pubsub.subscribe('DeleteTodo',this.DeleteTodo)
        // this.$bus.$on('DeleteTodo',this.DeleteTodo)
        this.$bus.$on('UpdataTodo',this.UpdataTodo)
    },
    beforeDestroy(){
        this.$bus.$off('CheckTodo')
        pubsub.unsubscribe(this.pubId)
        // this.$bus.$off('DeleteTodo')
    }

}
</script>

