<template>
  <li>
    <label>
        <!-- <input type="checkbox" :checked="todoItem.completed" @click="handleCheck(todoItem.id)"> -->
        <input type="checkbox" :checked="todoItem.completed" @change="handleCheck(todoItem.id)">
        <!-- <input type="checkbox" v-model="todoItem.completed"> -->
        <!-- 为checkbox，且为布尔值->v-model,但直接修改了props传下来的值，所以不推荐（不要卡bug），不报错是因为vue这里不监测属性值，但直接改对象（即修改内存地址）后vue会直接报错 -->
        <span v-show="!todoItem.isEdit">{{todoItem.title}}</span>
        <input
            type="text" 
            v-show="todoItem.isEdit"
            :value="todoItem.title"
            @blur="handleBlur(todoItem,$event)"
            ref="inputTitle"
            >
    </label>
    <button class="btn btn-danger" @click="handleDelete(todoItem.id)">删除</button>
    <button class="btn btn-edit" v-show="!todoItem.isEdit" @click="handleEdit(todoItem)">编辑</button>
</li>
</template>

<script>
import pubsub from 'pubsub-js'
export default {
    name:"MyItem",
    props:['todoItem'],
    methods:{
        //选中
        handleCheck(id){
            // this.CheckTodo(id);
            this.$bus.$emit('CheckTodo',id);
            // console.log(id);
        },
        //删除
        handleDelete(id){
            if(confirm('要走了嘛QAQ')){
                // console.log('guofen');
                // alert('heng~')
                // this.DeleteTodo(id);
                // this.$bus.$emit('DeleteTodo',id);
                pubsub.publish('DeleteTodo',id);
            }
            // this.itemDelete(id);
            // console.log(id);
        },
        //编辑
        handleEdit(todoItem){
            // todoItem.isEdit=true

            if(!todoItem.hasOwnProperty('isEdit')){
                this.$set(todoItem,'isEdit',true)
                // console.log('Add isEdit property')
            }else{
                // console.log('change isEdit')
                todoItem.isEdit=true
            }
            // console.log(todoItem.isEdit)
            // this.$refs.inputTitle.focus()
            //执行后才解析模板，也就是if/else操作后继续执行获取焦点的功能代码，但此时由于没解析模板，即输入框未出现，导致，获取焦点这步无效

            //解决方式：
            //一、使用定时器，保证解析模板完后一段事件才重新执行该操作
            // setTimeout(()=>{
            //     this.$refs.inputTitle.focus()
            // },0)
            // 二、（常用）使用￥nextTick，下一次dom更新完毕后再执行该操作
            this.$nextTick(function(){
                this.$refs.inputTitle.focus()
            })
        },
        //失去焦点事件,回调修改方法
        handleBlur(todoItem,e){
            this.todoItem.isEdit=false;
            if(!e.target.value.trim()){
                return alert('输入不能为空')
            }
            // console.log('UpdataTodo',todoItem.id,e.target.value)
            this.$bus.$emit('UpdataTodo',todoItem.id,e.target.value)
        }
    }
}
</script>

<style scoped>
li{
    list-style: none;
    height: 36px;
    line-height: 36px;
    padding:0 5px;
    border-bottom:1px solid #ddd;
}
li label{
    float:left;
    cursor:pointer;    
}
li label li input{
    vertical-align:middle;
    margin-right:6px;
    position:relative;
    top:-1px;
}
li button{
    float:right;
    display: none;
    margin-top:3px;
}
li:before{
    content:initial;
}
li:last-child{
    border-bottom:none;
}

li:hover{
    background-color:#ddd;
}
li:hover button{
    display: block;
    /* background-color:#ddd; */
}

</style>