<template>
    <div class="todo-header">
        <!-- <input type="text" placeholder="请输入你的任务名称，按回车键确认" v-model="title" @keyup.enter="add"> -->
        <input type="text" placeholder="请输入你的任务名称，按回车键确认" @keyup.enter="add"/>
    </div>
</template>

<script>
import {nanoid} from 'nanoid'
export default {
    name:"MyHeader",
    data(){
        return {title:''}
    },
    // // props传入函数  
    // props:['AddTodo'],
    methods:{
        add(e){
            if(e.target.value.trim()===''){
                e.target.value='';
                return alert("输入不能为空或全空格，请重新输入");
            }
            const todoObj={
                id:nanoid(),
                title:e.target.value,
                completed:false,
            }
            // console.log(todoObj)
            this.$emit('AddTodo',todoObj)
            //采用自定义绑定事件
            e.target.value='';
        }
    }
}
</script>

<style scoped>
.todo-header input{
    width:560px;
    height:28px;
    font-size:14px;
    border: 1px solid #ccc;
    border-radius: 4px;
    padding:4px 7px;
}

.todo-header input:focus{
    outline: none;
    border-color:rgba(82,168,236,0.8);
    box-shadow: inset 0 1px 1px rgba(255,255,255,0.075),0 0 8px rgba(82,168,236,0.6);
}
</style>