<template>
<div class="todo-footer" v-show="total">
    <label>
        <!-- <input type="checkbox" :checked="isAll" @change="checkAll"/> -->
        <!-- v-model这里不绑定props，适合用 -->
        <input type="checkbox" v-model="isAll"/>
    </label>
    <span>
        <span>已完成{{completedTotal}}</span>/ 全部{{total}}
    </span>
    <button class="btn btn-danger" @click="clearAll">清除已完成任务</button>
</div>
</template>

<script>
export default {
    name:'MyFooter',
    props:['todos'],
    computed:{
        total(){
            return this.todos.length;
        },
        completedTotal(){
            let i=0;
            return this.todos.reduce((pre,current)=>pre+(current.completed?1:0),0)
        },
        isAll:{
            get(){
                return this.todos.length===this.completedTotal&&this.todos.length!==0
            },
            set(checked){
                this.$emit('CheckAllTodo',checked)
                // this.CheckAllTodo(checked);
            }
            
        }//计算属性完整写法。get，获取计算属性的值，set处理计算属性

    },
    methods:{
        checkAll(e){
            // console.log(e.target.checked);
            this.$emit('CheckAllTodo',e.target.checked);
        },
        clearAll(){
            this.$emit('ClearAllTodo');
        }
    }

}
</script>

<style scoped>
.todo-footer{
    height: 40px;
    line-height: 40px;
    padding-left: 5px;
    margin-top:5px;
}
.todo-footer label{
    display: inline-block;
    margin-right: 20px;
    cursor:pointer;
}
.todo-footer label input{
    position:relative;
    top:-1px;
    vertical-align:middle;
    margin-right: 5px;
}
.todo-footer button{
    float:right;
    margin-top:5px;
}
</style>